






















































import { Component, Vue } from 'vue-property-decorator';
import ScanSigned from '@/componets/scan_signed.vue';
import {notes, NoteType} from '@/note';

import { Button, Field } from 'vant';
import { Row, Col } from 'vant';

import 'vant/lib/button/style';
import 'vant/lib/field/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';

Vue.use(Row).use(Col);
Vue.use(Button);
Vue.use(Field);

@Component({
  components:{ScanSigned},
})
export default class bScanSign extends Vue{

  private bgColor='#DFF4EF';
  private bCode='';
  private bShowSign=false;
  private bShowSigned=false;
  private bShowConfirm=true;
  private BusinessSign='';


  public created(): void {
    this.$store.state.title = this.$t('business.scanSign');
  }

  private cancelBusinessSign():void{
    this.bShowSign=false;
    this.bShowConfirm=true;
  }
  private businessSignment():void{
    if(this.BusinessSign){
    this.bCode = this.BusinessSign;
    this.bShowSign=false;
    this.bShowSigned=true;
  }else{
      notes.add({type: NoteType.Error, message: this.$t('notifyMsg.inputSign').toString()});
    }
  }
  private confirmBusiness():void{
    this.bShowConfirm=false;
    this.bShowSign = true;
  }
}
